import React, { Component } from 'react'
import M from 'materialize-css';
import './paymentDetails.css'
import WarningComponent from './WarningComponent';

class PaymentDetails extends Component {
  componentDidMount() {
    M.AutoInit();
  }
  render() {
    
    return (
      <div className="paymentDetails">
        <h4> </h4>
        <form action="" className='row'>
          <div className="input-field col s1">
            <select name="" id="payment_range">
              <option value="1"></option>
              <option value="2"></option>
              <option value="3"></option>
            </select>
            <label htmlFor="payment_range"> </label>
          </div>

          <div className="input-field col s2">
            <input id='startsDate' type="text" className="datepicker"/>
            <label htmlFor="startsDate">~<i className="material-icons right">date_range</i> </label>
          </div>

          <div className="input-field col s2">
            <input id='endsDate' type="text" className="datepicker"/>
            <label htmlFor="endsDate">~<i className="material-icons right">date_range</i> </label>
          </div>

          <div className="input-field col s1">
            <select name="" id="payment_counts">
              <option value="1">5</option>
              <option value="2">15</option>
              <option value="3">30</option>
            </select>
            <label htmlFor="payment_counts"> </label>
          </div>

          <div className="input-field col s3">
            <button className="btn grey darken-3">  </button>
          </div>

        </form>

        <div className="collection row">
          <div className="collection-item-wrapper">
            <i className="material-icons large">info_outline</i>
            <p> </p>
          </div>
        </div>

        <WarningComponent />

      </div>
    )
  }
}

export default PaymentDetails;