import React from 'react'
import { Link } from 'react-router-dom'
import './profile.css'

const ProfileSideNav = () => {
	return(
		<div className="col l4 profile-sidenav">
			<div className="collection with-header">
				<div className="collection-header"><h4></h4></div>
				<Link to='/profile' className="collection-item black-text"></Link>
				<Link to='/certifications' className="collection-item black-text"></Link>
				<Link to='/alarmsetting' className="collection-item black-text"></Link>
				<Link to='/changepwd' className="collection-item black-text"> </Link>
				<Link to='/withdrawal' className="collection-item black-text"></Link>
			</div>
		</div>
	)
}

export default ProfileSideNav