import React, { Component } from 'react'
import { Link } from 'react-router-dom';
import { NavLink } from 'react-router-dom';
import M from 'materialize-css';
import './mypageSidenav.css';

class MyPageSidenav extends Component {
  componentDidMount() {
    M.AutoInit();
  }

  render() {
    const { pathname } = this.props;
    const selectorValueForSidenav = pathname.includes('mypageBuyer');
    const finalResult = selectorValueForSidenav
      ? (
        <div>
          <div className='profile-infos'>
            <div className='btn-floating grey lighten-1'><i className="large material-icons">person</i></div>
            <h5>7292</h5>
            <Link to="/mypageProvider/requestsPage" className="btn-large red darken-3 waves-effect"><i className="material-icons left">repeat</i> </Link>
          </div>
          <ul className="collapsible z-depth-0">
            <li>
              <div className="collapsible-header"> <i className="material-icons right">arrow_drop_down</i></div>
              <div className="collapsible-body">
                <ul className="estimates-list">
                  <li><NavLink to="/mypageBuyer/estimate">(0)</NavLink></li>
                  <li><NavLink to="/mypageBuyer/estimate">(0)</NavLink></li>
                  <li><NavLink to="/mypageBuyer/estimate">(0)</NavLink></li>
                  <li><NavLink to="/mypageBuyer/estimate">(0)</NavLink></li>
                </ul>
              </div>
            </li>

            <li>
              <div className="collapsible-header"> <i className="material-icons right">arrow_drop_down</i></div>
              <div className="collapsible-body">
                <ul className="paymentDetails-list">
                  <li><NavLink to="/mypageBuyer/orderManage">(0)</NavLink></li>
                  <li><NavLink to="/mypageBuyer/orderManage"> (0)</NavLink></li>
                  <li><NavLink to="/mypageBuyer/orderManage">(0)</NavLink></li>
                  <li><NavLink to="/mypageBuyer/orderManage">(0)</NavLink></li>
                  <li><NavLink to="/mypageBuyer/orderManage"> (0)</NavLink></li>
                  <li><NavLink to="/mypageBuyer/orderManage">(0)</NavLink></li>
                </ul>
              </div>
            </li>

            <NavLink to='/mypageBuyer/coupons' className='collapsible-direct black-text'></NavLink>
            <NavLink to='/mypageBuyer/paymentDetails' className='collapsible-direct black-text'> </NavLink>
          
          </ul>
        </div>
      )
      : (
        <div>
          <div className='profile-infos'>
            <div className='btn-floating grey lighten-1'><i className="large material-icons">person</i></div>
            <h5>7292</h5>
            <Link to="/mypageBuyer/estimate" className="btn-large yellow darken-3 waves-effect"><i className="material-icons left">repeat</i> </Link>
          </div>
          <ul className="collapsible z-depth-0">
            <li>
              <div className="collapsible-header"> <i className="material-icons right">arrow_drop_down</i></div>
              <div className="collapsible-body">
                <ul className="estimates-list">
                  <li><NavLink to="/mypageProvider/requestsPage">(0)</NavLink></li>
                  <li><NavLink to="/mypageProvider/requestsPage">(0)</NavLink></li>
                  <li><NavLink to="/mypageProvider/requestsPage">(0)</NavLink></li>
                  <li><NavLink to="/mypageProvider/requestsPage">(0)</NavLink></li>
                </ul>
              </div>
            </li>

            <li>
              <div className="collapsible-header"> <i className="material-icons right">arrow_drop_down</i></div>
              <div className="collapsible-body">
                <ul className="paymentDetails-list">
                  <li><NavLink to="/mypageProvider/sellManage">(0)</NavLink></li>
                  <li><NavLink to="/mypageProvider/sellManage"> (0)</NavLink></li>
                  <li><NavLink to="/mypageProvider/sellManage">(0)</NavLink></li>
                  <li><NavLink to="/mypageProvider/sellManage">(0)</NavLink></li>
                  <li><NavLink to="/mypageProvider/sellManage"> (0)</NavLink></li>
                  <li><NavLink to="/mypageProvider/sellManage">(0)</NavLink></li>
                </ul>
              </div>
            </li>

            <NavLink to='/mypageProvider/myServices' className='collapsible-direct black-text'> </NavLink>
            <NavLink to='/mypageProvider/profitsManage' className='collapsible-direct black-text'> </NavLink>
            <NavLink to='/mypageProvider/profileSetting' className='collapsible-direct black-text'> </NavLink>
          </ul>
        </div>
      );

    return (
        <div>{ finalResult }</div>
    )
  }
}

export default MyPageSidenav;