import React from 'react'
import moment from 'moment';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { _proceed_order, _complete_order, _cancel_order } from '../../../store/actions/serviceAction';

const OrderManagementSummary = ({_proceed_order, _complete_order, _cancel_order, chk, purchaseList}) => {
  const proceed_order = (e, purchaseList_id) => {
    _proceed_order(purchaseList_id);
  }
  const complete_order = (purchaseList_id) => {
    _complete_order(purchaseList_id);
  }
  const cancel_order = (purchaseList_id) => {
    _cancel_order(purchaseList_id);
  }

  const disabled_proceed_btn = () => {
    if(purchaseList.cancel) return 'disabled';
    else if(purchaseList.hasOwnProperty('proceed')) return 'disabled';
    else return '';
  }
  const disabled_complete_btn = () => {
    if(purchaseList.cancel) return 'disabled';
    else if(purchaseList.proceed) return '';
    else return 'disabled';
  }
  const disabled_review_btn = () => {
    if(purchaseList.cancel) return 'disabled';
    else if(purchaseList.proceed === false) return '';
    else return 'disabled';
  }
  const disabled_cancle_btn = () => {
    if(purchaseList.cancel || (purchaseList.proceed === false)) return 'disabled';
  }

  let proceed_class = disabled_proceed_btn();
  let complete_class = disabled_complete_btn();
  let cancel_class = disabled_cancle_btn();
  let review_class = disabled_review_btn();
  

  return (
    <div className="collection notEmpty" key={purchaseList.date}>
      <div className="collection-item row">
        <div className="image-area col s4">
          <img src={purchaseList.imgURL} alt="" width={300} height={250}/>
        </div>
        <div className="options-area col s8">
          <p> : { purchaseList.options } </p>
          <p> : { moment(purchaseList.date.toDate()).format('YYYY / MM / DD HH:mm') }</p>
          <p> : { purchaseList.proceed ? <font color='blue'></font> : (purchaseList.proceed === false) ? <font color='blue'></font> : <font color='red'></font> }</p>
          <p> : { purchaseList.request ? <font color='blue'></font> : <font color='red'></font> }</p>
          <p> : { purchaseList.review ? <font color='blue'></font> : <font color='red'></font> }</p>
          <p> : { purchaseList.provider }</p> 
        </div>
      </div>

      <div className="collection-item row order-status">
        { (purchaseList.proceed === false) ? <div className="box blue white-text"></div> : null }
        { (purchaseList.hasOwnProperty('request') !== true) ? <div className="box orange white-text"></div> : null }
        { (purchaseList.proceed === true) ? <div className="box purple white-text"></div> : null }  
        { (purchaseList.proceed === false) ? <div className="box red white-text"></div> : null }
        { (purchaseList.cancel) ? <div className="box grey white-text"></div> : null }     
      </div>
      
      {
        (chk)
          ? (
            <div className="hidden-buttons row">
              <div className="col s2 btn waves-effect deep-purple lighten-2"></div>
              <Link to={'/purchaseDetails/' + purchaseList.service_id} className="col s2 btn waves-effect deep-purple lighten-2"></Link>                            
              <div className={"col s2 btn waves-effect deep-purple lighten-2 " + review_class}></div>
              <div className={"col s2 btn waves-effect deep-purple lighten-2 " + cancel_class}></div>
            </div>
          ) 
          : (
            <div className="hidden-buttons row">
              <div onClick={(e) => proceed_order(e, purchaseList.id)} className={"col s2 btn waves-effect deep-purple lighten-2 " + proceed_class}></div>
              <div className={"col s2 btn waves-effect deep-purple lighten-2 "}></div>
              <div onClick={() => complete_order(purchaseList.id)} className={"col s2 btn waves-effect deep-purple lighten-2 " + complete_class}></div>
              <div onClick={() => cancel_order(purchaseList.id)} className={"col s2 btn waves-effect deep-purple lighten-2 " + cancel_class}></div>
            </div>
          )
      }
      
    </div>
  )
}
const mapDispatchToProps = (dispatch) => {
  return {
    _proceed_order: (purchaseList_id) => dispatch(_proceed_order(purchaseList_id)),
    _complete_order: (purchaseList_id) => dispatch(_complete_order(purchaseList_id)),
    _cancel_order: (purchaseList_id) => dispatch(_cancel_order(purchaseList_id)),
  }
}
export default connect(null, mapDispatchToProps)(OrderManagementSummary);