import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './banner.css';

class Banner extends Component {
  render() {
    let path = this.props.type;       //   
    if(path === '/') path = 'sample'; //    ( or   )
 
    const btnInBanner = (path === 'sample')
      ? ( 
        <div className="container">
          <div className="btn-wrapper">
            <Link to='/thema' className='btnForMore white-text btn yellow darken-3 waves-effect waves-light'></Link>
            <Link to='/productIntro' className='btnForSubmit white-text btn yellow darken-3 waves-effect waves-light'></Link>
            <Link to='/community/admin' className='btnForSubmit white-text btn yellow darken-3 waves-effect waves-light'></Link>
          </div>
        </div>
      )
      : (
        <div className="container">
          <div className="btn-wrapper">
            <Link to='/registerProvider' className='btnForMore white-text btn yellow darken-3 waves-effect waves-light'></Link>
          </div>
        </div>
      )

    return(
      <header className='banner'>
        <img src={"/img/banner/" + path +'.jpg'} alt="" className='responsive-img'/>
        { btnInBanner }
      </header>
    )
  }
}

export default Banner;