import React, { Component } from 'react'
import M from 'materialize-css';
import WarningComponent from './WarningComponent';

class CouponPage extends Component {
  componentDidMount() {
    M.AutoInit();
  }
  render() {
    return (
      <div className="coupons">
        <form action="" className="row">
          <h4 className='col s4'>  <font color='red'>1</font></h4>
          <div className="input-field col s6">
            <input type="text" id="coupon_code"/>
            <label htmlFor="coupon_code">  </label>
          </div>
          <div className="input-field col s2">
            <button className="btn grey darken-3">  </button>
          </div>
        </form>

        <table style={{marginBottom: '3rem'}} className='centered'>
          <thead className='grey lighten-5'>
            <tr>
              <th></th>
              <th></th>
              <th> </th>
              <th> / </th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>   _1</td>
              <td className='red-text'>10,000</td>
              <td>10,000  </td>
              <td>2019.08.15 15:03 / (<font color='red'>2</font> )</td>
            </tr>
          </tbody>
        </table>
         
        <WarningComponent />
      </div>
    )
  }
}

export default CouponPage;