const initState = {
  authError: null
}

const authReducer = (state=initState, action) => {
  switch(action.type) {
    case 'LOGIN_ERROR':
      console.log('log in failed');
      return {...state, authError: 'Login Failed'}

    case 'LOGIN_SUCCESS':
      console.log('log in success');
      return {...state, authError: null}

    case 'SIGNOUT_SUCCESS':
      console.log('signout success');
      return state;

    case 'SIGNUP_SUCCESS':
      console.log('signup success');
      return {...state, authError: null}

    case 'SIGNUP_ERROR':
      console.log('signup error', action.err, action.err.message);
      return {...state, authError: action.err.message}

    case 'DELETE_SUCCESS':
      alert(' !');
      return state;
       
    case 'DELETE_ERROR':
      console.log('withdrawal failed')
      return state;
    
    case 'PWDUPDATE_SUCCESS':
      alert(' .');
      return state;
    
    case 'PWDUPDATE_ERROR':
      console.log('pwd update failed');
      return state;
    
    case 'REAUTHENTICATE_ERROR':
      alert('   .');
      return state; 
    
    case 'SENDRESETEMAIL_SUCCESS':
      alert('   .');
      return state;

    case 'SENDRESETEMAIL_ERROR':
      alert('    .');
      return state;
    
    case 'EMAILUSED_ERROR':
      alert('  .');
      return state;
    
    case 'EMAILINVALID_ERROR':
      alert('    .   .');
      return state;
    
    case 'OPERATION_ERROR':
      alert('   .   .');
      return state;
    
    case 'WEAKPWD_ERROR':
      alert('  .  .');
      return state;

    case 'SENDEMAILVERIFICATION_SUCCESS':
      console.log('send verification email')
      return state;
    
    case 'SENDEMAILVERIFICATION_ERROR':
      console.log('send verification email failed');
      return state;
    
    case 'PROFILEIMGREGISTER_SUCCESS':
      console.log('img register success');
      return state;
    
    case 'PROFILEIMGREGISTER_ERROR':
      console.log('img register error');
      return state;

    default:
      return state;
  }
}

export default authReducer;