import React, { Component, Fragment } from 'react';
import { Link } from 'react-router-dom';
import './purchase.css';
import M from 'materialize-css';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { firestoreConnect, isLoaded, isEmpty } from 'react-redux-firebase';
import { _buy_service } from '../../store/actions/serviceAction';

class Purchase extends Component {
  state = {
    selected : '',
  }
  componentDidUpdate(prevProps, prevState) {
    if(this.state.selected !== prevState.selected) {
      const hidden_modal = document.querySelector('#modal-verify-payment');
      const radioBtn = [...document.querySelectorAll('.with-gap')];
      
      M.Modal.init(hidden_modal, {
        dismissible: false,
        preventScrolling: false,
        onCloseEnd: () => radioBtn.filter(item => {
          if(item.checked) {
            this.setState({
              selected: item.id,
            })
          }
        })
      })
    }
  }
	componentDidMount() {
    M.AutoInit();
  }
  handleSubmit = (e) => {
    e.preventDefault();
    const hidden_modal = document.querySelector('#modal-verify-payment');
    const modal_instance = M.Modal.getInstance(hidden_modal);
    modal_instance.open();
  }
  handleChange = (e) => {
    this.setState({
      selected: e.target.id,
    })
  }
  close_modal = (e) => {
    e.preventDefault();
    this.setState({
      selected: '',
    })
  }
  buy_by_click = (e) => {
    e.preventDefault();
    this.props._buy_service(this.props.match.params.service_id, this.props.service, this.props.history);
  }
	render(){
    const { price, options } = this.props.location;
		return(
			<form onSubmit={this.handleSubmit} className="container purchase-page">
				<h4></h4>
				<div className="purchase-form">
					<table className="basic-purchase">
						<thead>
							<tr>
								<th></th>
								<th></th>
								<th></th>
							</tr>
						</thead>
						<tbody>
							<tr>
								<td>{ options ? options.map(item => <p key={item}>{item}</p>) : <p></p> }</td>
								<td>5</td>
								<td>{ price }</td>
							</tr>
						</tbody>
					</table>

					<table className="option-purchase">
						<thead>
							<tr>
								<th></th>
								<th></th>
								<th></th>
							</tr>
						</thead>
						<tbody>
							<tr>
								<td>
									<p>
										<label>
											<input type="checkbox"/>
											<span> </span>
										</label>
									</p>
								</td>
								<td>1</td>
								<td>30,000</td>
							</tr>
							<tr>
								<td>
									<p>
										<label>
											<input type="checkbox"/>
											<span>() </span>
										</label>
									</p>
								</td>
								<td>+0</td>
								<td>40,000</td>
							</tr>
							<tr>
								<td>
									<p>
										<label>
											<input type="checkbox"/>
											<span> </span>
										</label>
									</p>
								</td>
								<td>+3</td>
								<td>30,000</td>
							</tr>
							<tr>
								<td>
									<p>
										<label>
											<input type="checkbox"/>
											<span>   (1CAM) </span>
										</label>
									</p>
								</td>
								<td>+5</td>
								<td>230,000</td>
							</tr>
							<tr>
								<td>
									<p>
										<label>
											<input type="checkbox"/>
											<span>     </span>
										</label>
									</p>
								</td>
								<td>+5</td>
								<td>150,000</td>
							</tr>
						</tbody>
					</table>
				</div>

				<div className="purchase-price">
					<div className="panel-heading"></div>
					<div className="panel-body">
						<div className="row">
							<div className="col s7">
                <div className="row">
                  <div className="col s6">
                    <strong></strong>
                  </div>
                  <span className="col s6">
                    <button data-target="modal-coupon" className="btn-small waves-effect waves-light indigo modal-trigger"></button>
                  </span>
                  { /* Modal Structure */}
                  <div id="modal-coupon" className="modal">
                    <div className="modal-content">
                      <h5>  </h5>
                    </div>
                  </div>
                </div>

                <div className="row">
                  <div className="col s6">  </div>
                  <div className="col s6">
                    150,000
                  </div>
                </div>
							</div>

							<div className="col s5">
								<h5 className='col s6'> </h5>
								<h5 className="col s6">150,000</h5>
							</div>

						</div>
					</div>
				</div>
				<div className="purchase-method">
					<div className="panel-heading"></div>
					<div className="panel-body">
						<p>
							<label htmlFor='pay_creditcard'>
								<input onChange={this.handleChange} id='pay_creditcard' name="method" className="with-gap" type="radio" required />
								<span></span>
							</label>
						</p>
						<p>
							<label htmlFor='pay_kontobezahlen'>
								<input onChange={this.handleChange} id='pay_kontobezahlen' name="method" className="with-gap" type="radio" required/>
								<span> </span>
							</label>
						</p>
						<p>
							<label htmlFor='pay_bankaccount'>
								<input onChange={this.handleChange} id='pay_bankaccount' name="method" className="with-gap" type="radio" required/>
								<span></span>
							</label>
						</p>
						<p>
							<label htmlFor='pay_cellphone'>
								<input onChange={this.handleChange} id='pay_cellphone' name="method" className="with-gap" type="radio" required/>
								<span></span>
							</label>
						</p>
					</div>
				</div>
				<div className="tax">
					<div className="panel-heading"></div>
					<div className="panel-body">
						<li>    .</li>
						<li>() /     . [ ]</li>
						<li> /        .</li>
					</div>
				</div>
				<button className="btn-large waves-effect waves-light indigo right purchase-btn"></button>

        {
          (this.state.selected)
            ? (
              <Fragment>
              <input type='hidden' id='hidden_btn_for_additional_modal' data-target='modal-verify-payment' className="btn-large modal-trigger btn waves-effect" />
              <div id="modal-verify-payment" className="modal no-autoinit modal-fixed-footer">
                <div className="modal-content">
                  <h5>{this.state.selected === 'pay_creditcard' ? ' '
                        : this.state.selected === 'pay_kontobezahlen' ? '  '
                        : this.state.selected === 'pay_bankaccount' ? ' '
                        : this.state.selected === 'pay_cellphone' ? ' ' : ''}</h5>
                  <p>       LG( '' )              .</p>
                </div>
                <div className="modal-footer">
                  <button onClick={this.close_modal} className="modal-close btn-flat waves-effect left"></button>  
                  <button onClick={this.buy_by_click} className="btn-flat waves-effect right"></button>  
                </div>              
              </div>
              </Fragment>
            )
            : null
        }
			</form>
		)
	}
}
const mapStateToProps = (state) => {
  return {
    service: state.firestore.data.service,
  }
}
const mapDisptachToProps = (dispatch) => {
  return {
    _buy_service: (service_id, service, history) => dispatch(_buy_service(service_id, service, history)),
  }
}
export default compose(
  connect(mapStateToProps, mapDisptachToProps),
  firestoreConnect((props) => [
    { collection: 'services', doc: props.match.params.service_id, storeAs: 'service' }
  ])
)(Purchase);


