import React, { Component } from 'react'
import ProfileSideNav from './ProfileSideNav'
import { connect } from 'react-redux'
import { withdrawal } from '../../store/actions/authAction'
import { Redirect } from 'react-router-dom'
import './profile.css'

class Withdrawal extends Component {
	state = {
		email: ''
	}
	handleChange= (e) => {
		this.setState({
			[e.target.id] : e.target.value
		});
	}
	handleSubmit = (e) => {
		e.preventDefault();
		this.props.withdrawal(this.state);
	}
	render(){
	const { auth } = this.props;
	if(!auth.uid) return <Redirect to ='/' />
	return(
		<div className="container profile_deatails">
			<div className="row">
				<ProfileSideNav />
				<div className="col l8">
					<div className="withdrawal">
						<h6></h6>
						<div className="divider"></div>
						<div className="row">
							<div className="col l12">
								<form action="#">
									<p>
										<label>
											<input className="with-gap" type="radio" name="withdrawal-menu"/>
											<span>   </span>
										</label>
									</p>
									<p>
										<label>
											<input className="with-gap" type="radio" name="withdrawal-menu"/>
											<span> ,   </span>
										</label>
									</p>
									<p>
										<label>
											<input className="with-gap" type="radio" name="withdrawal-menu"/>
											<span>   </span>
										</label>
									</p>
									<p>
										<label>
											<input className="with-gap" type="radio" name="withdrawal-menu"/>
											<span>    </span>
										</label>
									</p>
									<p>
										<label>
											<input className="with-gap" type="radio" name="withdrawal-menu"/>
											<span>   </span>
										</label>
									</p>
									<p>
										<label>
											<input className="with-gap" type="radio" name="withdrawal-menu"/>
											<span> </span>
										</label>
									</p>
								</form>
							</div>
							<h6> </h6>
							<div className="divider"></div>
							<div className="input-field col l12">
								<form onSubmit = {this.handleSubmit}>
									<input type="text" className="validate" id="email" onChange= { this.handleChange } />
									<label htmlFor="email"> </label>
									<div>*       .</div>
									<p>    ,      .</p>
									<div className="input-field">
										<button className="btn indigo right"></button>
									</div>
								</form>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	)
}
}

const mapStateToProps = (state) => {
	return {
		auth: state.firebase.auth
	}
}

const mapDispatchToProps = (dispatch) => {
	return {
		withdrawal: (user) => dispatch(withdrawal(user))
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(Withdrawal);