import React, { Component, Fragment } from 'react'
import M from 'materialize-css';

class CreateServiceStep1 extends Component {
  componentDidUpdate(prevProps, prevState) {
    if(prevProps !== this.props) M.AutoInit();
  }

  render() {
    if(this.props.currentStep !==1) return null;

    return (
      <Fragment>
        <h4 className='center'>  !</h4>
        <h5 className="center">  !</h5>

        <div className="input-field select-priority col s10 offset-s1">
          <select name="" id="priority1" className='_required' value={this.props.priority1} onChange={this.props.handleChange} >
            <option value="" disabled>1  ()</option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
          <label htmlFor="priority1">  - 1</label>
          { this.props.need && !this.props.priority1 ? <span className='red-text'><i className='material-icons'>error</i>  !</span> : null }
        </div>
                
        {
          (this.props.priority1) 
            ? (
              <div className="input-field select-priority col s10 offset-s1">
                <select name="" id="priority2" className='' value={this.props.priority2} onChange={this.props.handleChange}>
                  <option value="" disabled>2  ()</option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
                <label htmlFor="priority2">  - 2</label>
              </div>
            )
            : null
        }
        {
          (this.props.priority2)
            ? (
              <div className="input-field select-priority col s10 offset-s1">
                <select name="" id="priority3" className='' value={this.props.priority3} onChange={this.props.handleChange}>
                  <option value="" disabled>3  ()</option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
                <label htmlFor="priority3">  - 3</label>
              </div>
            )
            : null
        }
      </Fragment>
    )
  }
}

export default CreateServiceStep1;